/**
 *
 * LoadingIndicatorWrapper
 * This component is used as a wrapper around the loading indicator to solve an issue I saw where if the
 * LoadingIndicator was used directly, it would flash on the screen due to a full re-render
 * I think that because this is a pure component, as opposed to a SFC, we get the benefit of the component not re-rendering if
 * the props don't change
 */

import React from 'react';
// import styled from 'styled-components';

import LoadingIndicator from 'components/LoadingIndicator';

interface LoadingIndicatorWrapperProps {
  show: boolean;
}

class LoadingIndicatorWrapper extends React.PureComponent<
  LoadingIndicatorWrapperProps,
  {}
> {
  // eslint-disable-line react/prefer-stateless-function
  render() {
    const { show } = this.props;
    return <div>{show && <LoadingIndicator />}</div>;
  }
}

export default LoadingIndicatorWrapper;



