/**
 *
 * HoverListItem
 *
 */

import React from 'react';
import styled from 'styled-components';
import styleVars from 'cssOverrides/styleVars';

const ListItem = styled.div`
  border-top: solid 1px ${styleVars.gray300};
  margin-bottom: 0 !important;
  :last-child {
    border-bottom: solid 1px ${styleVars.gray300};
  }
  &:hover {
    background: ${styleVars.gray100};
  }
`;

interface HoverListItemProps {
  showPointerCursor?: boolean;
  className?: string;
}

class HoverListItem extends React.Component<HoverListItemProps> {
  render() {
    const { showPointerCursor, className } = this.props;
    return (
      <ListItem
        className={`${className} ${showPointerCursor ? 'c-pointer' : ''}`}
      >
        {this.props.children}
      </ListItem>
    );
  }
}

export default HoverListItem;



