import { createSelector } from 'reselect';
import { STORE_KEY } from 'containers/Deploy/constants';
import { State } from 'containers/Deploy/reducer';
import { AppState } from 'reducers';

/**
 * Direct selector to the deploy state domain
 */
const selectDeployDomain = () => (state: AppState): State => state[STORE_KEY];

/**
 * Other specific selectors
 */

/**
 * Default selector used by Deploy
 */

const makeSelectDeploy = () =>
  createSelector(selectDeployDomain(), (substate) => substate);

export default makeSelectDeploy;
export { selectDeployDomain };



