/*
 *
 * Deploy
 *
 */

import React from 'react';
import { compose } from 'redux';
import { connect } from 'react-redux';

import injectReducer from 'utils/injectReducer';
import injectSaga from 'utils/injectSaga';

import { Button } from 'reactstrap';

import {
  deploymentActions,
  deploymentSelectors,
} from 'containers/Console/ducks/deployments';
import DeployPage from 'components/DeployPage';
import ConsolePage from 'components/ConsolePage';
import PageHeader from 'components/ConsolePageHeader';
import consoleMessages from 'containers/Console/messages';
import { AddIcon, DeployIcon } from 'components/EntityIcons';

import { createOnDismissModal, createOnAddNewDeployment } from './actions';
import makeSelectDeploy from './selectors';
import { FormattedMessage } from 'react-intl';
import messages from './messages';

import reducer from './reducer';
import saga from './sagas';
import { STORE_KEY } from './constants';
import { DeploymentConfig } from 'cb-utils/console-entity-models';
import { SystemRouteProps } from 'cb-utils/systemRouteProps';
import NewDeployModal from 'components/NewDeploymentModal';
import { createCreateDeploymentRequest } from '../DeployDetail/actions';
import Helmet from 'components/DeployHelmet';
import { CBDispatch } from 'cb-utils/typeUtils';
import LearnMoreLink from 'components/LearnMoreLink';
import { createStructuredSelector } from 'reselect';

interface DeployProps extends SystemRouteProps<{}> {}

export class Deploy extends React.PureComponent<
  DeployProps & DispatchToProps & StateToProps,
  {}
> {
  systemKey = this.props.match.params.systemKey;

  componentDidMount() {
    this.props.init(this.systemKey);
  }

  render() {
    const {
      deployments,
      onDismissModal,
      deployPage,
      createNewDeployment,
    } = this.props;
    return (
      <React.Fragment>
        <Helmet />
        {deployPage.isNewDeploymentModalOpen && (
          <NewDeployModal
            toggle={onDismissModal}
            create={createNewDeployment}
            isCreating={deployPage.isCreatingDeployment}
            error={deployPage.createError}
          />
        )}
        <ConsolePage
          subheader={this.renderSubHeader()}
          isFetchingAssets={this.props.isFetchingDeployments}
          zeroState={{
            show: this.props.totalDeployments === 0,
            icon: <DeployIcon />,
            title: consoleMessages.assetClassDeployments,
            info: messages.zeroStateInfo,
            moreInfo: (
              <LearnMoreLink url="https://docs.clearblade.com/v/3/1-platform_concepts/Deployments/" />
            ),
            addButton: this.renderAddButton({ size: 'lg' }),
          }}
        >
          <DeployPage deployments={deployments} />
        </ConsolePage>
      </React.Fragment>
    );
  }

  renderSubHeader = () => {
    return (
      <PageHeader
        header={
          <div>
            <DeployIcon />{' '}
            <FormattedMessage {...consoleMessages.assetClassDeployments} />
          </div>
        }
        actionButton={this.renderAddButton}
      />
    );
  };

  renderAddButton = ({ size = 'md' }) => (
    <Button
      size={size}
      onClick={this.props.createOnAddNewDeployment}
      color="primary"
      data-cy="add-deployment"
    >
      <AddIcon />
      &nbsp;
      <FormattedMessage {...messages.addDeployment} />
    </Button>
  );
}

type StateToProps = ReturnType<typeof mapStateToProps>;

const mapStateToProps = createStructuredSelector({
  deployments: deploymentSelectors.wholeSlice,
  deployPage: makeSelectDeploy(),
  isFetchingDeployments: deploymentSelectors.fetchAll.isLoading,
  totalDeployments: deploymentSelectors.entitiesCount,
});

type DispatchToProps = ReturnType<typeof mapDispatchToProps>;
function mapDispatchToProps(dispatch: CBDispatch, ownProps: DeployProps) {
  return {
    init: (sysKey: string) =>
      dispatch(deploymentActions.fetchAll.request(sysKey)),
    onDismissModal: () => dispatch(createOnDismissModal()),
    createOnAddNewDeployment: () => dispatch(createOnAddNewDeployment()),
    createNewDeployment: (config: DeploymentConfig) =>
      dispatch(
        createCreateDeploymentRequest(ownProps.match.params.systemKey, config),
      ),
  };
}

const withConnect = connect(mapStateToProps, mapDispatchToProps);

const withReducer = injectReducer({ key: STORE_KEY, reducer });
const withSaga = injectSaga({ key: STORE_KEY, saga });

export default compose(withReducer, withSaga, withConnect)(Deploy);



