/**
 *
 * DeploymentCard
 *
 */

import React, { SFC } from 'react';
import { DeploymentConfig } from 'cb-utils/console-entity-models';
import { withRouter } from 'react-router';
import { SystemRouteProps } from 'cb-utils/systemRouteProps';
import Desc from 'components/CardDescription';
import NavCard from 'components/NavToDetailCard';

interface DeploymentCardProps extends SystemRouteProps<{}> {
  deployment: DeploymentConfig;
}

const DeploymentCard: SFC<DeploymentCardProps> = (props) => {
  const roleName = props.deployment.name;
  return (
    <NavCard name={roleName}>
      <div>{roleName}</div>
      <div>
        <Desc>{props.deployment.description}</Desc>
      </div>
    </NavCard>
  );
};

export default withRouter(DeploymentCard);



