/**
 *
 * CardTable
 *
 */

import React from 'react';
import styled from 'styled-components';
import { CaretUpIcon, CaretDownIcon } from '../EntityIcons';
import { Button, ButtonGroup, Col } from 'reactstrap';
import { DownloadIcon } from 'components/Icons';
import { ICypressTarget } from 'utils/types';
const ButtonRow = styled.div`
  display: flex;
  margin: 0 0 0.5rem 0;
`;

const FullDiv = styled.div`
  width: 100%;
`;

const Header = styled.div`
  margin-bottom: 0.75rem;
`;

const CardWrapper = styled.div`
  > div {
    margin-bottom: 1rem;
  }
`;

export enum SortDirection {
  ASC,
  DESC,
}

export interface HeaderColumn {
  component: JSX.Element;
  isSortable?: boolean;
  sortKey?: string;
  width?: number;
}

interface CardTableProps extends ICypressTarget {
  headerColumns?: HeaderColumn[];
  onSortChange?: (sortKey: string, sortDirection: SortDirection) => void;
  initialSortDirection?: SortDirection;
  initialSortKey?: string;
  exportCallback?: () => void;
  childContainer?: React.ComponentClass;
}

interface CardTableState {
  sortDirection: SortDirection;
  sortKey: string;
}

class CardTable extends React.Component<CardTableProps, {}> {
  // eslint-disable-line react/prefer-stateless-function

  static defaultProps: CardTableProps = {
    headerColumns: [],
    initialSortDirection: SortDirection.ASC,
    initialSortKey: '',
  };

  state: CardTableState = {
    sortDirection: this.props.initialSortDirection,
    sortKey: this.props.initialSortKey,
  };

  render() {
    const ChildContainer = this.props.childContainer || CardWrapper;
    return (
      <FullDiv data-cy={this.props['data-cy']}>
        <Header>
          <ButtonRow>
            {this.props.headerColumns.map(this.renderHeader)}
            {this.props.exportCallback && (
              <ButtonGroup size="sm" className="ml-auto float-right">
                <div className="float-right">
                  <Button
                    className="btn btn-secondary"
                    onClick={this.props.exportCallback}
                  >
                    <DownloadIcon />
                  </Button>
                </div>
              </ButtonGroup>
            )}
          </ButtonRow>
        </Header>
        <ChildContainer>{this.props.children}</ChildContainer>
      </FullDiv>
    );
  }

  renderHeader = (h: HeaderColumn, idx: number) => {
    return (
      <Col xs={h.width ? h.width : undefined} key={`h-column-${idx}`}>
        <span
          className={h.isSortable && 'c-pointer'}
          onClick={() => this.handleClick(h)}
        >
          {h.component}
          &nbsp;
          {this.state.sortKey === h.sortKey &&
            this.renderSortIcon(this.state.sortDirection)}
        </span>
      </Col>
    );
  };

  handleClick = (h: HeaderColumn) => {
    if (h.isSortable) {
      this.handleSortChange(h.sortKey);
    }
  };

  renderSortIcon(dir: SortDirection) {
    if (dir === SortDirection.ASC) {
      return <CaretUpIcon />;
    }
    return <CaretDownIcon />;
  }

  handleSortChange = (key: string) => {
    const newDirection =
      this.state.sortDirection === SortDirection.ASC
        ? SortDirection.DESC
        : SortDirection.ASC;
    this.props.onSortChange(key, newDirection);
    this.setState({
      ...this.state,
      sortDirection: newDirection,
      sortKey: key,
    });
  };
}

export default CardTable;



