import { all, takeEvery, call, put } from 'redux-saga/effects';

import { ActionConstants as DeployActionConstants } from 'containers/DeployDetail/actionConstants';
import { createDeployment } from 'cb-utils/services/deployment';
import { deploymentActions } from 'containers/Console/ducks/deployments';
import {
  CreateDeploymentSuccess,
  CreateDeploymentRequest,
  createCreateDeploymentSuccess,
  createCreateDeploymentError,
} from 'containers/DeployDetail/actions';
import { push } from 'react-router-redux';
import parseCbError from 'cb-utils/parseCbError';
import { detailUrls } from 'cb-utils/getUrls';

function* callRefreshDeployments(action: CreateDeploymentSuccess) {
  yield put(deploymentActions.fetchAll.request(action.payload.systemKey));
}

export function* createDeploymentSaga() {
  yield takeEvery(
    [
      DeployActionConstants.CREATE_DEPLOYMENT_SUCCESS,
      DeployActionConstants.DELETE_DEPLOYMENT_SUCCESS,
    ],
    callRefreshDeployments,
  );
}

export function* createDeploymentRequestSaga() {
  yield takeEvery(DeployActionConstants.CREATE_DEPLOYMENT_REQUEST, function*(
    action: CreateDeploymentRequest,
  ) {
    try {
      yield call(
        createDeployment,
        action.payload.systemKey,
        action.payload.deployment,
      );
      yield put(createCreateDeploymentSuccess(action.payload.systemKey));
      yield put(push(detailUrls.deployments(action.payload.deployment.name)));
    } catch (e) {
      yield put(createCreateDeploymentError(parseCbError(e)));
    }
  });
}

// All sagas to be loaded
export default function*() {
  yield all([createDeploymentSaga(), createDeploymentRequestSaga()]);
}



