/**
 *
 * NewDeploymentModal
 *
 */

import React from 'react';
// import styled from 'styled-components';

import { Form, Input, Label, FormGroup, Button } from 'reactstrap';

import { FormattedMessage } from 'react-intl';
import messages from './messages';
import consoleMessages from 'containers/Console/messages';

import InfoModal from 'components/InfoModal';
import { DeploymentConfig } from 'cb-utils/console-entity-models';

import LoadingIndicator from 'components/LoadingIndicator';

interface NewDeploymentModalProps {
  toggle: () => void;
  create: (role: DeploymentConfig) => void;
  isCreating: boolean;
  error: string;
}

interface NewDeploymentModalState {
  deployment: DeploymentConfig;
}

class NewDeploymentModal extends React.Component<
  NewDeploymentModalProps,
  NewDeploymentModalState
> {
  // eslint-disable-line react/prefer-stateless-function

  state: NewDeploymentModalState = {
    deployment: {
      name: '',
      description: '',
      edges: [],
      assets: [],
    },
  };

  render() {
    return (
      <InfoModal
        toggleOpen={this.props.toggle}
        isOpen
        title={<FormattedMessage {...messages.modalTitle} />}
        body={this.renderBody()}
        footer={() => this.renderFooter()}
      />
    );
  }

  renderBody() {
    const { name, description } = this.state.deployment;
    return (
      <Form
        onSubmit={(e) => {
          e.preventDefault();
          this.props.create(this.state.deployment);
        }}
      >
        <FormGroup>
          <Label>
            <FormattedMessage {...consoleMessages.name} />
          </Label>
          <Input
            value={name}
            name="deployment-name"
            onChange={(e) =>
              this.handleDeploymentPropChange('name', e.target.value)
            }
            type="text"
            autoFocus
            placeholder="DeploymentName"
          />
        </FormGroup>
        <FormGroup>
          <Label>
            <FormattedMessage {...consoleMessages.description} />
          </Label>
          <Input
            value={description}
            name="role-description"
            onChange={(e) =>
              this.handleDeploymentPropChange('description', e.target.value)
            }
            type="textarea"
          />
        </FormGroup>
        <div className="invalid-feedback" style={{ display: 'block' }}>
          {this.props.error}
        </div>
      </Form>
    );
  }

  renderFooter() {
    const { create, isCreating } = this.props;
    const { deployment } = this.state;
    return (
      <Button
        type="submit"
        disabled={deployment.name === ''}
        color="primary"
        onClick={() => create(deployment)}
      >
        {isCreating ? (
          <LoadingIndicator />
        ) : (
          <FormattedMessage {...messages.createDeployment} />
        )}
      </Button>
    );
  }

  handleDeploymentPropChange(field: keyof DeploymentConfig, value: string) {
    this.setState({
      ...this.state,
      deployment: {
        ...this.state.deployment,
        [field]: value,
      },
    });
  }
}

export default NewDeploymentModal;



