/**
 *
 * CardTableForEntities
 *
 */

import React from 'react';
import { HeaderColumn, SortDirection } from '../CardTable';
import { EntityModel } from 'containers/Console/state';
import CardTable from 'components/CardTable';
import { ICypressTarget } from 'utils/types';
// import styled from 'styled-components';

interface CardTableForEntitiesProps extends ICypressTarget {
  headerColumns: HeaderColumn[];
  defaultSortKey: string;
  list?: any[];
  entities?: EntityModel;
  entityCard: React.ComponentClass;
  cardProp: string;
  exportCallback?: () => void;
  otherProps?: object;
}

interface CardTableForEntitiesState {
  sortKey: string;
  sortDirection: SortDirection;
}

class CardTableForEntities extends React.Component<
  CardTableForEntitiesProps,
  CardTableForEntitiesState
> {
  // eslint-disable-line react/prefer-stateless-function

  state: CardTableForEntitiesState = {
    sortKey: this.props.defaultSortKey,
    sortDirection: SortDirection.ASC,
  };

  render() {
    const {
      headerColumns,
      defaultSortKey,
      entities,
      exportCallback,
      list,
    } = this.props;
    return (
      <CardTable
        headerColumns={headerColumns}
        onSortChange={this.handleSortChange}
        initialSortDirection={SortDirection.ASC}
        initialSortKey={defaultSortKey}
        exportCallback={exportCallback}
        data-cy={this.props['data-cy']}
      >
        {this.renderEntities(
          entities ? Object.values(entities.entities) : list,
        )}
      </CardTable>
    );
  }

  renderEntities = (entitiesList: any[]) => {
    const { entityCard: EntityCard, cardProp, otherProps } = this.props;
    return entitiesList
      .sort((a, b) => {
        let keyA = a[this.state.sortKey];
        let keyB = b[this.state.sortKey];
        if (typeof keyA === 'string' && typeof keyB === 'string') {
          keyA = keyA.toUpperCase();
          keyB = keyB.toUpperCase();
        }
        if (typeof keyA === 'object' || typeof keyB === 'object') {
          // if object, sort by if it exists and isn't empty
          keyA = typeof keyA === 'object' ? Object.keys(keyA).length : -1;
          keyB = typeof keyB === 'object' ? Object.keys(keyB).length : -1;
        }
        if (keyA < keyB) {
          return this.state.sortDirection === SortDirection.ASC ? -1 : 1;
        }
        if (keyA > keyB) {
          return this.state.sortDirection === SortDirection.ASC ? 1 : -1;
        }

        // names must be equal
        return 0;
      })
      .map((e, idx) => (
        <EntityCard key={idx} {...{ [cardProp]: e, otherProps }} />
      ));
  };

  handleSortChange = (key: string, dir: SortDirection) => {
    this.setState({
      ...this.state,
      sortDirection: dir,
      sortKey: key,
    });
  };
}

export default CardTableForEntities;



