/*
 *
 * Deploy reducer
 *
 */

import assign from 'immutable-assign';
import { ActionTypes } from 'containers/Deploy/actions';
import { ActionTypes as DeployActionTypes } from 'containers/DeployDetail/actions';
import { ActionConstants } from 'containers/Deploy/actionConstants';
import { ActionConstants as DeploymentActionConstants } from 'containers/DeployDetail/actionConstants';

export interface State {
  isNewDeploymentModalOpen: boolean;
  isCreatingDeployment: boolean;
  createError: string;
}

const initialState: State = {
  isNewDeploymentModalOpen: false,
  isCreatingDeployment: false,
  createError: null,
};

function deployReducer(
  state: State = initialState,
  action: ActionTypes | DeployActionTypes,
) {
  switch (action.type) {
    case ActionConstants.ON_ADD_NEW_DEPLOYMENT:
      return assign(state, (s) => s.isNewDeploymentModalOpen, () => true);
    case ActionConstants.ON_DISMISS_DEPLOYMENT_MODAL:
      return assign(state, (s) => {
        s.isNewDeploymentModalOpen = false;
        return s;
      });
    case DeploymentActionConstants.CREATE_DEPLOYMENT_REQUEST:
      return assign(state, (s) => {
        s.isCreatingDeployment = true;
        s.createError = null;
        return s;
      });
    case DeploymentActionConstants.CREATE_DEPLOYMENT_SUCCESS:
      return assign(state, (s) => {
        s.isNewDeploymentModalOpen = false;
        s.isCreatingDeployment = false;
        s.createError = null;
        return s;
      });
    case DeploymentActionConstants.CREATE_DEPLOYMENT_ERROR:
      return assign(state, (s) => {
        s.createError = action.payload.error;
        s.isCreatingDeployment = false;
        return s;
      });
    default:
      return state;
  }
}

export default deployReducer;



