/**
 *
 * DeployPage
 *
 */

import React from 'react';
// import styled from 'styled-components';

import { FormattedMessage } from 'react-intl';
import messages from './messages';
import LoadingIndicatorWrapper from 'components/LoadingIndicatorWrapper';

import CardTable from 'components/CardTableForEntities';
import { Entities, DeploymentModel } from 'cb-utils/console-entity-models';
import DeploymentCard from 'components/DeploymentCard';

const defaultSortKey = 'name';
const createColumnsForCardTable = () => {
  return [
    {
      component: (
        <b>
          <FormattedMessage {...messages.deploymentGridNameColumn} />
        </b>
      ),
      isSortable: true,
      sortKey: defaultSortKey,
    },
  ];
};

interface DeployPageProps {
  deployments: Entities<DeploymentModel>;
}

class DeployPage extends React.PureComponent<DeployPageProps, {}> {
  // eslint-disable-line react/prefer-stateless-function

  render() {
    const { deployments } = this.props;
    return deployments.isFetching ? (
      <LoadingIndicatorWrapper show />
    ) : (
      this._renderDeploymentTable(deployments)
    );
  }

  _renderDeploymentTable = (deployments: Entities<DeploymentModel>) => {
    return (
      <CardTable
        defaultSortKey={defaultSortKey}
        headerColumns={createColumnsForCardTable()}
        entities={deployments}
        entityCard={DeploymentCard}
        cardProp="deployment"
      />
    );
  };
}

export default DeployPage;



