/**
 *
 * NavToDetailCard
 *
 */

import React, { SFC } from 'react';
import { CardContent } from 'material-ui-next/Card';
import styled from 'styled-components';
import { withRouter } from 'react-router';
import { SystemRouteProps } from 'cb-utils/systemRouteProps';
import HoverListItem from 'components/HoverListItem';
import LinkWrapper from 'components/LinkWithoutDecoration';
interface NavToDetailCardProps extends SystemRouteProps<{}> {
  name: string;
  customUrl?: any;
}

const AnchorWrap = styled.a`
  text-decoration: none !important;
`;

const NavToDetailCard: SFC<NavToDetailCardProps> = (props) => {
  const content = (
    <CardContent className="c-pointer">{props.children}</CardContent>
  );
  return (
    <HoverListItem>
      {props.customUrl ? (
        <AnchorWrap href={props.customUrl} target="_blank">
          {content}
        </AnchorWrap>
      ) : (
        <LinkWrapper to={`${props.match.url}/${props.name}/edit`}>
          {content}
        </LinkWrapper>
      )}
    </HoverListItem>
  );
};

export default withRouter(NavToDetailCard);



